const xui = require('../lib/index').default;

async function App() {
    let count = xui.addState(1);
    console.log(count.value)
    count.update(count.value + 1);
    console.log(count.value)
    return xui.nsx(`
        <div>
            <h1>count: ${count.value}</h1>
            <p>This is a simple example of a template literal</p>
        </div>
   `)
 
}

module.exports = App;